.switch {
  width: 60px;
  height: 34px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

.switch-input {
  height: 0;
  width: 0;
  opacity: 0;
}

.switch-slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switch-slider:before {
  content: "";
  cursor: pointer;
  height: 26px;
  width: 26px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.switch-input:checked + .switch-slider {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switch-input:checked + .switch-slider:before {
  transform: translateX(26px);
}

